.App {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #1e3c72, #2a5298);
  color: white;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
}

.App h1 {
  font-size: 3rem;
  margin-bottom: 30px;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
}

select.city-select {
  width: 100%;
  max-width: 320px;
  padding: 12px 16px;
  font-size: 18px;
  border-radius: 10px;
  border: 2px solid #4fc3f7;
  background-color: #ffffffcc;
  color: #333;
  margin-bottom: 40px;
  transition: all 0.3s ease;
  cursor: pointer;
}

select.city-select:hover,
select.city-select:focus {
  border-color: #0288d1;
  box-shadow: 0 0 10px #0288d1aa;
  outline: none;
}

.weather-container {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 18px;
  padding: 30px;
  max-width: 700px;
  width: 100%;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
}

.weather-container h2 {
  font-weight: 700;
  margin-bottom: 25px;
  text-shadow: 1px 1px 6px rgba(0,0,0,0.5);
}

@media (max-width: 768px) {
  .App h1 {
    font-size: 2.2rem;
  }
  .weather-container {
    padding: 20px;
  }
}
