.weather-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(to bottom right, #2c3e50, #3498db);
  min-height: 100vh;
  color: white;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px 0;
}
.daily-weather-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin-top: 30px;
}

.weather-container h2 {
  font-size: 2.5rem;
  margin-bottom: 40px;
  font-weight: 600;
}

.weather-card {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 20px;
  margin: 15px;
  width: 280px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  text-align: center;
  transition: transform 0.3s ease;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.weather-card:hover {
  transform: scale(1.05);
}

.weather-card strong {
  font-size: 1.2rem;
  color: #f1c40f;
}

.icon {
  font-size: 3rem;
  margin: 10px 0;
}

@media (min-width: 768px) {
  .weather-container {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
}
.hourly-weather {
  margin-top: 30px;
  width: 90%;
  max-width: 900px;
  color: white;
}

.hourly-scroll {
  display: flex;
  overflow-x: auto;
  gap: 12px;
  padding: 10px 0;
}

.hourly-card {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 15px;
  min-width: 110px;
  text-align: center;
  flex-shrink: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
}
.hourly-card .icon {
  font-size: 1.2rem;
}

@media (max-width: 480px) {
  .hourly-card {
    min-width: 100px;
    padding: 14px 12px;
  }
  .hourly-card strong {
    font-size: 1.1rem;
  }
}

.hourly-card p {
  font-size: 14px;
  margin: 6px 0;
  position: relative;
  padding-left: 24px;
}

.icon-wrapper {
  position: relative;
  display: block;
  margin-bottom: 8px;
  color: white;
  font-size: 1rem;
  padding-right: 24px; 
}

.icon-wrapper::after {
  position: absolute;
  right: 0; 
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.2rem;
  pointer-events: none;
}

