/* CitySelector.css */

.city-select {
  width: 100%;
  max-width: 300px;
  padding: 10px 15px;
  font-size: 16px;
  border: 2px solid #3498db;
  border-radius: 8px;
  background-color: white;
  color: #333;
  outline: none;
  transition: border-color 0.3s ease;
  cursor: pointer;
}

.city-select:focus {
  border-color: #2980b9;
  box-shadow: 0 0 5px rgba(41, 128, 185, 0.7);
}
